.TodoItem {
    display: grid;
    grid-template: 
        'check text change delete';

    margin-bottom: var(--size_s);
    padding: var(--size_s);
    border-bottom: 1px solid var(--light);

    list-style: none;

    & .check {
        grid-area: check;

        width: var(--size_m_double);
        height: var(--size_m_double);

        background-color: var(--light);
        border: 1px solid var(--dark);
        border-radius: 50%;

        &.success {
            background-color: var(--success);
        }
    }

    & .todoBody {
        grid-area: text;
        flex-grow: 1;

        background-color: var(--main);
        color: var(--font_main_color);
        padding: var(--size_s);

        resize: none;
        
    }

    & .btn {
        border: none;
        color: var(--font_main_color);

        &.change {
            grid-area: change;

            background-color: var(--accent_color);
            width: 70px;
        }
        &.delete {
            grid-area: delete;

            background-color: var(--danger_color);
            width: 40px;
            border-radius: 0 5px 5px 0;
        }
    }
}
