	--size_m_third: calc(var(--size_m) * 3);
	--size_m_half: calc(var(--size_m) / 2);

	--size_l: 20px;
	--size_l_double: calc(var(--size_l) * 2);
	--size_l_third: calc(var(--size_l) * 3);
	--size_l_half: calc(var(--size_l) / 2);
}

.darkTheme {
